html,body,.app{height:100%}body{margin:0;padding:0;font-family:'Economica', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:20px}body header .no-margin,body main .no-margin{margin:0}a{text-decoration:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3,h4,h5,h6{font-family:'Dosis', sans-serif}main{height:calc(100% - 59px);padding-top:59px}main>div[role='group']{height:100%}.app__content{margin:0 auto;max-width:800px;padding:1em 40px}.text-center{text-align:center}.Toastify__toast-container{top:75px}.Toastify__toast-body{font-family:'Economica', sans-serif}@media all and (min-width: 768px){.hide-medium-up{display:none}}@media all and (min-width: 768px){main{height:calc(100% - 74px);padding-top:74px}}
